}

.Collapsible__trigger {
    text-decoration: underline;
    cursor: pointer;
}

table {
    width: 100%;
}

th {
    margin: 0;
    background: #4e495f;
    padding: 6px 13px;
}

td {
    margin: 0;
    text-align: left;
    padding: 4px 11px;
}

td:first-child {
    width: 30%;
    text-align: right;
}

tr:nth-child(even) {
    background: #4e495f;
}

tr:nth-child(odd) {
    background: #816271;
    background: #585369;
}

/* React Modal */
.ReactModal__Content {
    opacity: 0;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.ReactModal__Content--after-open{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: 
        opacity 50ms ease-in-out,
        -webkit-transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 
        opacity 50ms ease-in-out,
        transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 
        opacity 50ms ease-in-out,
        transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
        -webkit-transform 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.ReactModal__Content--before-close{
    opacity: 0;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 
        opacity 300ms ease-in-out,
        -webkit-transform 300ms ease-out;
    transition: 
        opacity 300ms ease-in-out,
